.footer {
  background-color: $footerbg;
  padding-top: 7.5rem;
  padding-bottom: 6rem;
}
@media screen and (max-width: $ipad) {
  .footer {
    padding-bottom: 3rem;
    padding-top: 3rem;
  }
}
.footer .container {
  display: flex;
}
@media screen and (max-width: $ipadPro) {
  .footer .container {
    flex-wrap: wrap;
    justify-content: flex-start;
  }
}
.footer .container .footer-title {
  min-width: $iphone;
  max-width: 50%;
  width: 100%;
  flex: 1 0 10rem;
}
@media screen and (max-width: $ipadPro) {
  .footer .container .footer-title {
    max-width: 100%;
    margin-bottom: 3rem;
  }
}
.footer .container .footer-title .footer-slogan {
  font-weight: 900;
  color: $white;
  transition: $transition;
}
.footer .container .footer-title .footer-slogan span {
  display: block;
  font-size: 4rem;
  transition: $transition;
}
@media screen and (max-width: $ipadPro) {
  .footer .container .footer-title .footer-slogan span {
    display: inline-block;
    font-size: 3rem;
  }
}
.footer .container .footer-menu {
  display: flex;
  flex: 1 0 auto;
}
@media screen and (max-width: $ipad) {
  .footer .container .footer-menu {
    min-width: $iphoneH;
    justify-content: flex-start;
    flex-wrap: wrap;
  }
}
@media screen and (max-width: $iphoneH) {
  .footer .container .footer-menu {
    min-width: auto;
  }
}
.footer .container .footer-menu ul {
  flex: 1 0 auto;
  padding: 0;
}
@media screen and (max-width: $ipad) {
  .footer .container .footer-menu ul {
    min-width: 50%;
  }
}
.footer .container .footer-menu ul a {
  line-height: 200%;
  font-weight: 600;
  color: $white;
  transition: $transition;
  font-size: 25px;
  min-width: 160px;
}
.footer .container .footer-menu ul a:hover {
  color: $specialRed;
  cursor: pointer;
}
@media screen and (max-width: $iphone) {
  .footer .container .footer-menu ul a {
    font-size: 17px;
  }
}
.footer .container .footer-copy {
  display: flex;
  width: 100%;
  align-items: center;
  margin-top: 5rem;
  flex-wrap: wrap;
}
.footer .container .footer-copy .copy {
  color: $darkhover;
  font-size: 18px;
  max-width: 50%;
  width: 100%;
  min-width: $iphone;
  flex: 1 1 10rem;
}
.footer .container .footer-copy .footer-column {
  margin: 0 auto 0 0;
  flex: 1 0 50%;
  font-weight: 600;
}
.footer .container .footer-copy .footer-column.footer-button {
  margin: 0 auto 0 auto;
  flex: 1 1 20rem;
  display: flex;
}
@media screen and (max-width: $iphone5) {
  .footer .container .footer-copy .footer-column.footer-button {
    flex: 1 0 $iphone5;
  }
}
@media screen and (max-width: $ipad) {
  .footer .container .footer-copy {
    flex-wrap: wrap;
  }
  .footer .container .footer-copy .copy {
    order: 1;
    margin-top: 3rem;
    min-width: 100%;
  }
}
@media screen and (max-width: $iphone) {
  .footer .container .footer-copy .copy {
    font-size: 13px;
  }
}
